import {findSelectById, SelectContainer} from "../../../ui/select";

export default function () {

    let nomenclatureSelect:SelectContainer = findSelectById('nomenclature_id');
    let unitSelect:SelectContainer = findSelectById('unit_id');

    nomenclatureSelect.addOnChangeEventListener(function (nomenclature) {
        unitSelect.processItems(nomenclature.metadata.units);
        unitSelect.setPlaceholderText();
    });

}